<div class="main__products-item"><div class="main__products-item__top"><img class="item-img" :src="require(`@/assets/images/main/${product.img}`)"></div><div class="main__products-item__bottom"><div class="main__products-item__title">{{product.title}}</div><div class="main__products-item__rev"><div class="main__products-item__stars"><img src="@/assets/icons/star.svg" v-for="star in product.stars" :key="star"></div><h3 class="main__products-item__coun-rev">({{product.reviews}})</h3></div><p class="main__products-item__descr">{{product.descr}}</p><div class="main__products-item__btns"><button class="main__products-item__btn">Купить на OZON</button><button class="main__products-item__btn">Купить на WB</button></div></div></div>