<section class="main__comments"><h2 class="main__comments-title">Почитайте отзывы довольных клиентов</h2><swiper class="main__comments-slider" :slidesPerView="1" :spaceBetween="-50" :modules="modules" :loop="true" :navigation="{prevEl:'.main__comments-btn.prev', nextEl:'.main__comments-btn.next'}"><swiper-slide v-for="item in getUsersComments" :key="item.id"><Comment :item="item"></Comment></swiper-slide></swiper><div class="main__comments-contanier"><div class="main__comments-btns"><button class="main__comments-btn prev"><i class="fa-solid fa-chevron-left icon"></i></button><button class="main__comments-btn next"><i class="fa-solid fa-chevron-right icon"></i></button></div></div></section>