<section class="main_products"><div class="main__products-search"><section-title>Наши товары</section-title><div class="input-block"> <input class="main__products-input" placeholder="Поиск"><i class="fa-solid fa-magnifying-glass icon"></i></div></div><h2 class="main__products-title">Популярные теги:</h2><div class="main__products-tags"><div class="main__products-tag" v-for="tag in tagsList" :key="tag.id" :class="{active: tag.active}" @click="tag.active = !tag.active">{{tag.title}}<button v-if="tag.active" @click="tag.active = false"><i class="fa-solid fa-xmark icon"></i></button></div></div><div class="main__products-block"> <ProductItem v-for="product in getProductList" :key="product.id" :product="product"></ProductItem></div><div class="main__products-btn"> </div></section>