<footer class="footer"><div class="footer__container"><div class="footer__block"><div class="footer__block-icons"><a class="footer__block-icon" href="#!" v-for="icon in getFooterIcons" :key="icon.id"><img :src="require(`@/assets/icons/${icon.icon}`)"></a></div><div class="footer__block-info"><div class="footer__block-item" v-for="item in getFooterInfo" :key="item.id"><button class="footer__block-btn"><img :src="require(`@/assets/icons/${item.icon}`)"></button><div class="footer__block-div"><p class="footer__block-title">{{item.title}}</p><a class="footer__block-text" href="#!">{{item.info}}</a></div></div></div><div class="footer__block-bottom"><ul class="footer__block-menu"><li v-for="link in getMenuList" :key="link.id"><a class="footer__block-link" href="#!">{{link.title}}</a></li></ul><p class="footer__block-copyright">© 2000-2022, Все права защищены</p></div></div></div></footer>